import { toast, ToastOptions } from 'react-toastify'

import APIMiddleware from '@savchenko91/rc-redux-api-mw'

import { TOKEN } from '@/constant/localStorage'

const ERRORS_RU = {
  'Internal Server Error': '–û—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞: –æ–±—Ä–∞—Ç–∏—Ç–µ—Å—å –∫ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø–æ—Ä—Ç–∞–ª–∞, –µ—Å–ª–∏ –æ—à–∏–±–∫–∞ –Ω–µ –∏—Å—á–µ–∑–Ω–µ—Ç',
  'Not Found': '–û—à–∏–±–∫–∞: –û–±—Ä–∞—Ç–∏—Ç–µ—Å—å –∫ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø–æ—Ä—Ç–∞–ª–∞',
  'TypeError: Failed to fetch': '–û—à–∏–±–∫–∞: –ù–µ —É–¥–∞–ª–æ—Å—å —Å–¥–µ–ª–∞—Ç—å –∑–∞–ø—Ä–æ—Å. –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –∏–Ω—Ç–µ—Ä–Ω–µ—Ç —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ',
}

const props: ToastOptions = {
  type: 'error',
}

let messages: string[] = []

const api = new APIMiddleware({
  headers: () => {
    return new Headers({
      'Content-Type': 'application/json',
      Authorization: `Bearer ${localStorage.getItem(TOKEN)}`,
    })
  },
  async onFail({ body, response, error }) {
    const err =
      (body as Record<'errorDescription' | 'error', string>)?.errorDescription ||
      (body as Record<'errorDescription' | 'error', string>)?.error ||
      ERRORS_RU[(error || response?.statusText) as keyof typeof ERRORS_RU] ||
      error ||
      response?.statusText

    if (!err) return

    if (!messages.includes(err)) {
      toast(err, props)
    }

    messages.push(err)

    setTimeout(() => {
      messages = []
    }, 1000)
  },
})

export default api
